
header {
	position: relative;
	height: 50px; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 5; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300; }
	&_item {
		border-right: 2px dotted black;
		padding-right: 28px; } }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);
	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	&_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }
	&_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }
	&_phone {
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }
	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		&:hover {
			background-image: none;
			background-color: $main-color; } } }





.promo {
	min-height: 800px;
	background: url('../img/bg/main_bg.jpg') center (center / cover) no-repeat;
	padding: 150px 0 135px 0;
	&_header {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px;
		margin: 0;
		text-align: center; }
	&_subheader {
		color: #ffffff;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }
	&_descr {
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }
	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		&:hover {
			background-image: none;
			background-color: $main-color; } } }


.reasons {
	padding: 71px 0 65px 0; }

.label {
	position: relative;
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		background-color: #fff;
		color: #000; } }

.title {
	position: relative;
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	line-height: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	z-index: 2;
	&_white {
		color: #fff; } }

.subtitle {
	position: relative;
	margin-top: 19px;
	color: #222222;
	font-size: 17px;
	font-weight: 400;
	text-align: center;
	z-index: 2;
	&_white {
		color: #fff; } }

.reasons .reasons_block {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 35px;
	.reasons_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 113px;
		height: 113px;
		background-color: $main-color;
		border-radius: 100%; }
	.reasons_descr {
		width: 343px;
		.reasons_subtitle {
			line-height: 21px;
			color: $main-color;
			font-size: 17px;
			font-weight: 400; }
		.reasons_text {
			color: #202020;
			font-size: 15px;
			font-weight: 300;
			line-height: 21px; } } }

.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url('../img/bg/mobile_app.png') center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/bg/mobile_screen_1.jpg') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/bg/mobile_screen_2.jpg') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/bg/mobile_screen_3.jpg') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/bg/mobile_screen_4.jpg') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/bg/mobile_screen_5.jpg') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/bg/mobile_screen_6.jpg') center (center / cover) no-repeat; }
		&_subtitle {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			line-height: 31px;
			text-transform: uppercase;
			text-align: center; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			margin: 0 auto;
			border-radius: 100%;
			cursor: pointer; } } }

.choice {
	position: relative;
	min-height: 895px;
	padding: 57px 0 138px 0;
	background: url('../img/bg/mobile_app.png') center (center / cover) no-repeat;
	&_img {
		position: relative;
		display: block;
		margin: 56px auto 0 auto;
		z-index: 2; }
	&_descr {
		text-align: center;
		color: #222222;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px; }
	.black {
		position: absolute;
		top: 0;
		left: 0;
		height: 499px;
		width: 100%;
		background-color: #070716;
		z-index: 1; } }

.requier {
    padding: 68px 0 84px 0;
    &_block {
        margin: 33px 0;
        &_red {
            margin-bottom: 0; } }

    &_title {
        color: #1ca8c3;
        font-size: 22px;
        font-weight: 400;
        &_red {
            color: #ff5656;
            font-size: 22px;
            font-weight: 400; } }
    &_list {
        list-style-type: none;
        margin-top: 22px;
        li {
            position: relative;
            color: #222222;
            font-size: 17px;
            font-weight: 300;
            line-height: 24px;
            margin-bottom: 10px;
            &:before {
                content: '';
                display: block;
                position: absolute;
                left: -32px;
                top: 2px;
                width: 21px;
                height: 20px;
                background: url('../icons/check_blue.svg') center (center / cover) no-repeat; } }
        &_red {
            list-style-type: none;
            margin-top: 22px;
            li {
                position: relative;
                color: #222222;
                font-size: 17px;
                font-weight: 300;
                line-height: 24px;
                margin-bottom: 10px;
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    left: -32px;
                    top: 2px;
                    width: 21px;
                    height: 20px;
                    background: url('../icons/check_red.svg') center (center / cover) no-repeat; } } } }
    &_descr {
        color: #1ca8c3;
        font-weight: 300; } }

.map {
	position: relative;
	min-height: 717px;
	padding: 49px 0;
	background: url('../img/bg/map.jpg') center (center / cover) no-repeat;
	&_link {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 49px;
		text-align: center;
		font-size: 15px;
		font-weight: 400;
		line-height: 68px;
		text-transform: uppercase;
		color: #1fbad6;
		&:hover {
			text-decoration: none;
			color: #fff; } } }

footer {
	padding: 39px 0 50px 0;
	position: relative; }

.divader {
	width: 299px;
	height: 4px;
	background-color: #1fbad6;
	margin: 0 auto; }
.footer {
	&_links {
		width: 360px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase;
				margin-top: 32px;
				&:hover {
					text-decoration: none;
					color: $main-color; } } }
		&_help {
			display: flex;
			justify-content: space-between;

			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase;
				margin-top: 15px;
				&:hover {
					text-decoration: none;
					color: red; } } }
		&_lang {
			display: flex;
			justify-content: center;
			a {
				color: #1fbad6;
				font-size: 11px;
				font-weight: 400;
				text-transform: uppercase;
				margin-top: 19px;
				&:hover {
					text-decoration: none;
					color: #a2ec18; } } } }
	&_mail {
		position: absolute;
		right: 1%;
		top: 40%; }

	&_social {
		position: absolute;
		left: 1%;
		top: 40%;
		width: 150px;
		&_link {
			display: flex;
			justify-content: space-between; } } }

